import {ApolloClient}from 'apollo-client'
import { InMemoryCache } from 'apollo-cache-inmemory'
import { HttpLink } from 'apollo-link-http'
import Repos from "../graphql/repository"
import CryptoJS from 'crypto-js'
export const config = (data)=>{
    if(!data)return
    var decrypted = CryptoJS.AES.decrypt(data.token,data.key);
    let token = decrypted.toString(CryptoJS.enc.Utf8);//è½¬åŒ–ä¸ºutf8
    //console.log(token)
    const httpLink = new HttpLink({
        uri: 'https://api.github.com/graphql', // é…ç½®è¯·æ±‚url 
        headers: { // é…ç½®header
            Authorization: `Bearer ${token}`
        }
    })
    const client =  new ApolloClient({
        link:  httpLink,
        cache: new InMemoryCache() // ç¼“å­˜
    })  
    // å¢åŠ é…ç½®
    const repos = new Repos(data)
    return{
        client,repos
    }
}
