import defaultState from '../state.js'
import * as Types from '../../actions/ReposActionsType'
function setIssue(state = defaultState.config,action){
    switch(action.type){
        case Types.SET_ISSUE:
        state.issue = action.data
        //console.log("å¾—åˆ°",state,"data",action,action.type)
        //return state
        return Object.assign({},state) //ç›´æ¥è¿”å›stateæ˜¯ä¸ä¼šè‡ªåŠ¨çš„åˆ·æ–°çš„
        default:
        return state
    }
}
function setIssues(state = defaultState.config,action){
    switch(action.type){
        case Types.SET_ISSUES:
        state.issues = action.data
        console.log("å¾—åˆ°",state,"data",action,action.type)
        //return state
        return Object.assign({},state)
        default:
        return state
    }
}
function setConfig (state = defaultState.config,action){
    switch(action.type){
        case Types.SET_CONFIG:
        state.config = action.data
        //console.log("è¿›å…¥",state,"data",action,action.type)
        return state
        case Types.SET_CLIENT:
        state.client = action.data
        //console.log("è¿›å…¥",state,"data",action,action.type)
        return state
        default:
        //console.log("å¾—åˆ°",state,"data",action,action.type)
        return state
    }
}
function pageTitle (state = defaultState.pageTitle,action) {
// ä¸åŒçš„actionæœ‰ä¸åŒçš„å¤„ç†é€»è¾‘
    switch (action.type) {
        case 'SET_PAGE_TITLE':
        return action.data
        default:
        return state
    }
}

function user (state = defaultState.user, action){
    switch (action.type) {
        case 'SET_USER':
        return action.data
        default:
        return state
    }
}

export {
    setIssue,
    setIssues,
    setConfig,
    pageTitle,
    user
}